.finances-container
{
    max-width: 1200px;
    display: flex;    
    margin: 0 auto;
    font-size: 1.2em;
    padding: 10px;
    flex-wrap: wrap;
}
ul
{
    margin: 0 auto;
    list-style: none;
    text-align: center;
    transition: 0.5s;
    cursor: pointer;
}
ul:hover
{
    background: #bbbb;
}
ul li
{
    border: 1px solid black;
}
button
{
    width: 100px;
    margin: 0 auto;
    padding: 5px 10px;
    border-radius: 5px;
    transition: 0.4s;
}
button:hover
{
    background: green;
}
@media screen and (max-width:1024px)
{
    ul
    {
        flex-basis: 50%;   
    }
    ul:last-child
    {
        margin: 0;
        text-align: center;
    }
}
@media screen and (max-width:425px)
{
    ul
    {
        flex-basis: 100%;   
    }
}